import alphaWl from "./alpha";
import regWl from "./reg";
import superWL from "./super";
import whitelistedAddresses from "./whitelisted";

const getProof = (address) => {
    const signaturesMap = new Map();
    const NFTVaucherVoid = {
        'wlLevel': 0,
        'signature': '0x85172260f044eda169e87596daf474480dab25293847e35d621d7d20b2f9c9f912c720ec83fc04daaf8e5895b7ef91209dcf8ffacd23fac56c01c5afcd1796ab1c'
    }

    whitelistedAddresses.forEach(v => {
        let voucher = {};
        voucher.wlLevel = v.wlLevel;
        voucher.signature = v.signature;
        signaturesMap.set(v.buyer.toLowerCase(), voucher);
    })
    regWl.forEach(v => {
        let voucher = {};
        voucher.wlLevel = v.wlLevel;
        voucher.signature = v.signature;
        signaturesMap.set(v.buyer.toLowerCase(), voucher);
    })
    superWL.forEach(v => {
        let voucher = {};
        voucher.wlLevel = v.wlLevel;
        voucher.signature = v.signature;
        signaturesMap.set(v.buyer.toLowerCase(), voucher);
    })
    alphaWl.forEach(v => {
        let voucher = {};
        voucher.wlLevel = v.wlLevel;
        voucher.signature = v.signature;
        signaturesMap.set(v.buyer.toLowerCase(), voucher);
    })
    const NFTVaucher = signaturesMap.get(address.toLowerCase());


    if (typeof (NFTVaucher) !== "undefined" && NFTVaucher !== null) {

        return NFTVaucher;
    }

    return NFTVaucherVoid;
}

export default getProof;