const initialState = {
  loading: false,
  name: "",
  totalSupply: 0,
  cost: 0,
  whitelistIsOn: true,
  paused: false,
  maxSupply: 4888,
  batchSupply: 888,
  batch: 1,
  batchMinted: 0,
  batchMintedForBuyer: 0,
  batchSaleIsOn: true,
  wlCost: 0,
  superWlCost: 0,
  alphaWlCost: 0,
  maxMintAmountPerTx: 2,
  superWLMaxMintAmountPerTx: 3,
  alphaWlMaxMintAmountPerTx: 5,
  error: false,
  errorMsg: "",
  ownNfts: []
};

const dataReducer = (state = initialState, action) => {
  switch (action.type) {
    case "CHECK_DATA_REQUEST":
      return {
        ...state,
        loading: true,
          error: false,
          errorMsg: "",
      };
    case "CHECK_DATA_SUCCESS":

      return {
        ...state,
        loading: false,


          totalSupply: action.payload.totalSupply,
          whitelistIsOn: action.payload.whitelistIsOn,
          paused: action.payload.paused,
          maxSupply: action.payload.maxSupply,
          batch: action.payload.batch,
          batchSupply: action.payload.batchSupply,
          batchMinted: action.payload.batchMinted,
          batchMintedForBuyer: action.payload.batchMintedForBuyer,
          maxMintAmountPerWl: action.payload.maxMintAmountPerWl,
          maxMintAmountPerSuperWl: action.payload.maxMintAmountPerSuperWl,
          maxMintAmountPerAlphaWl: action.payload.maxMintAmountPerAlphaWl,


          // totalSupply: action.payload.totalSupply
          // cost: action.payload.cost,
          // whitelistIsOn: action.payload.whitelistIsOn,
          // paused: action.payload.paused,
          // maxSupply: action.payload.maxSupply,
          // batchSupply: action.payload.batchSupply,
          // batch: action.payload.batch,
          // batchMinted: action.payload.batchMinted,
          // batchMintedForBuyer: action.payload.batchMintedForBuyer,
          // batchSaleIsOn: action.payload.batchSaleIsOn,
          // wlCost: action.payload.wlCost,
          // superWlCost: action.payload.superWlCost,
          // alphaWlCost: action.payload.alphaWlCost,
          // maxMintAmountPerTx: action.payload.maxMintAmountPerTx,
          // superWLMaxMintAmountPerTx: action.payload.superWLMaxMintAmountPerTx,
          // alphaWlMaxMintAmountPerTx: action.payload.alphaWlMaxMintAmountPerTx,
          // maxMintAmountPerWl: action.payload.maxMintAmountPerWl,
          // maxMintAmountPerSuperWl: action.payload.maxMintAmountPerSuperWl,
          // maxMintAmountPerAlphaWl: action.payload.maxMintAmountPerAlphaWl,

          error: false,
          errorMsg: "",
      };
    case "CHECK_DATA_FAILED":
      return {
        ...initialState,
        loading: false,
          error: true,
          errorMsg: action.payload,
      };
    default:
      return state;
  }
};

export default dataReducer;