export const fetchData = (account, wlLevel) => {
  return async (dispatch) => {
    dispatch(fetchDataRequest());
    let totalSupply = -1;
    let whitelistIsOn = null;
    let paused = null;
    let maxSupply = 4888;
    let batch = -1;
    let batchSupply = -1;
    let batchMinted = -1;
    let batchMintedForBuyer = -1;
    let maxMintAmountPerWl = -1;
    let maxMintAmountPerSuperWl = -1;
    let maxMintAmountPerAlphaWl = -1;
    const start = performance.now();
    try {

      while (paused === null) {
        paused = await store
          .getState()
          .blockchain.readContract.methods.paused()
          .call();
      }
      console.log("paused:", paused);
      while (whitelistIsOn === null) {
        whitelistIsOn = await store
          .getState()
          .blockchain.readContract.methods.whitelistMintEnabled()
          .call();
      }
      console.log("whitelistIsOn:", whitelistIsOn);

      while (totalSupply === -1) {
        totalSupply = await store
          .getState()
          .blockchain.readContract.methods.totalSupply()
          .call();
      }
      console.log("whitelistIsOn:", whitelistIsOn);

      while (batchSupply === -1) {
        batchSupply = await store
          .getState()
          .blockchain.readContract.methods.batchSupply()
          .call();
      }
      console.log("batchSupply:", batchSupply);
      batchSupply = batchSupply - 1;

      // let cost = await store
      //   .getState()
      //   .blockchain.readContract.methods.cost()
      //   .call();
      // console.log("cost:", cost);

      // let wlCost = await store
      //   .getState()
      //   .blockchain.readContract.methods.wlCost()
      //   .call();
      // console.log("wlCost:", wlCost);
      // let superWlCost = await store
      //   .getState()
      //   .blockchain.readContract.methods.superWlCost()
      //   .call();
      // console.log("superWlCost:", superWlCost);
      // let alphaWlCost = await store
      //   .getState()
      //   .blockchain.readContract.methods.alphaWlCost()
      //   .call();
      // console.log("alphaWlCost:", alphaWlCost);
      // let maxMintAmountPerTx = await store
      //   .getState()
      //   .blockchain.readContract.methods.maxMintAmountPerTx()
      //   .call();
      // console.log("maxMintAmountPerTx:", maxMintAmountPerTx);
      // maxMintAmountPerTx = maxMintAmountPerTx - 1;

      // let superWLMaxMintAmountPerTx = await store
      //   .getState()
      //   .blockchain.readContract.methods.superWLMaxMintAmountPerTx()
      //   .call();
      // console.log("superWLMaxMintAmountPerTx:", superWLMaxMintAmountPerTx);
      // superWLMaxMintAmountPerTx = superWLMaxMintAmountPerTx - 1;
      // let alphaWlMaxMintAmountPerTx = await store
      //   .getState()
      //   .blockchain.readContract.methods.alphaWlMaxMintAmountPerTx()
      //   .call();
      // console.log("alphaWlMaxMintAmountPerTx:", alphaWlMaxMintAmountPerTx);
      // alphaWlMaxMintAmountPerTx = alphaWlMaxMintAmountPerTx - 1;
      // let batchSaleIsOn = await store
      //   .getState()
      //   .blockchain.readContract.methods.batchSaleEnabled()
      //   .call();
      // console.log("batchSaleIsOn:", batchSaleIsOn);
      while (maxMintAmountPerWl === -1) {
        maxMintAmountPerWl = await store
          .getState()
          .blockchain.readContract.methods.maxMintAmountPerWl()
          .call();
      }
      console.log("maxMintAmountPerWl:", maxMintAmountPerWl);
      maxMintAmountPerWl = maxMintAmountPerWl - 1;

      while (maxMintAmountPerSuperWl === -1) {
        maxMintAmountPerSuperWl = await store
          .getState()
          .blockchain.readContract.methods.maxMintAmountPerSuperWl()
          .call();
      }
      console.log("maxMintAmountPerSuperWl:", maxMintAmountPerSuperWl);
      maxMintAmountPerSuperWl = maxMintAmountPerSuperWl - 1;

      while (maxMintAmountPerAlphaWl === -1) {
        maxMintAmountPerAlphaWl = await store
          .getState()
          .blockchain.readContract.methods.maxMintAmountPerAlphaWl()
          .call();
      }
      console.log("maxMintAmountPerAlphaWl:", maxMintAmountPerAlphaWl);
      maxMintAmountPerAlphaWl = maxMintAmountPerAlphaWl - 1;

      while (batch === -1) {
        batch = await store
          .getState()
          .blockchain.readContract.methods.batch()
          .call();
      }
      console.log("Batch" + batch)
      while (batchMintedForBuyer === -1) {
        batchMintedForBuyer = await store
          .getState()
          .blockchain.readContract.methods.batchMintedForBuyer(account, wlLevel)
          .call();
      }

      while (batchMinted === -1) {
        batchMinted = await store
          .getState()
          .blockchain.readContract.methods.batchMinted()
          .call();
      }
      console.log("BatchMinted" + batchMinted)

      console.log({
        totalSupply,
        whitelistIsOn,
        paused,
        maxSupply,
        batch,
        batchSupply,
        batchMinted,
        batchMintedForBuyer,
        maxMintAmountPerWl,
        maxMintAmountPerSuperWl,
        maxMintAmountPerAlphaWl
      });
      dispatch(
        fetchDataSuccess({
          totalSupply,
          whitelistIsOn,
          paused,
          maxSupply,
          batch,
          batchSupply,
          batchMinted,
          batchMintedForBuyer,
          maxMintAmountPerWl,
          maxMintAmountPerSuperWl,
          maxMintAmountPerAlphaWl
        })
      );
    } catch (err) {
      console.log(err);
      dispatch(fetchDataFailed("Could not load data from contract."));
    } finally {
      const duration = performance.now() - start;
      console.log("Duration:" + duration)
    }
  };
};
