// constants
import Web3EthContract from "web3-eth-contract";
import Web3 from "web3";
import abi from "../../contracts/mmc5";
import detectEthereumProvider from '@metamask/detect-provider'
// log
import {
  fetchData
} from "../data/dataActions";

const connectRequest = () => {
  return {
    type: "CONNECTION_REQUEST",
  };
};

const connectSuccess = (payload) => {
  return {
    type: "CONNECTION_SUCCESS",
    payload: payload,
  };
};

const connectFailed = (payload) => {
  return {
    type: "CONNECTION_FAILED",
    payload: payload,
  };
};

const updateAccountRequest = (payload) => {
  return {
    type: "UPDATE_ACCOUNT",
    payload: payload,
  };
};

export const connect = () => {
  return async (dispatch) => {
    dispatch(connectRequest());
    const PROVIDER_URL = "https://mainnet.infura.io/v3/4f6c6bdd0f5c4db781d7354b705506bc";
    const getData = new Web3(new Web3.providers.HttpProvider(PROVIDER_URL));

    const provider = await detectEthereumProvider();
    if (provider) {
      const ethereum = window.ethereum;

      Web3EthContract.setProvider(provider);
      let web3 = new Web3(provider);
      // try {
      //   // check if the chain to connect to is installed
      //   await ethereum.request({
      //     method: 'wallet_switchEthereumChain',
      //     params: [{
      //       chainId: '0x1'
      //     }], // chainId must be in hexadecimal numbers
      //   });
      // } catch (error) {
      //   // This error code indicates that the chain has not been added to MetaMask
      //   // if it is not, then install it into the user MetaMask

      //   console.error(error);
      // }
      try {
        const accounts = await ethereum.request({
          method: "eth_requestAccounts",
        });
        web3.eth.handleRevert = true;
        const chainId = await ethereum.request({
          method: 'eth_chainId'
        });
        console.log("chain id: " + chainId);

        if (1 === 1) {

          const SmartContractObj = new Web3EthContract(
            abi, "0xB9252199A4c33399f5DCdCA04d972b8c727b1cFe"
          );
          const readContract = new web3.eth.Contract(abi, "0xB9252199A4c33399f5DCdCA04d972b8c727b1cFe"
            //"0xB9252199A4c33399f5DCdCA04d972b8c727b1cFe"
          );
          //"0x466d60c4C560220D73563262FDC4b2cA384DE215"
          dispatch(
            connectSuccess({
              account: accounts[0],
              smartContract: SmartContractObj,
              readContract: readContract,
              web3: web3,
              getData: getData
            })
          );
          // Add listeners start
          ethereum.on("accountsChanged", (accounts) => {
            dispatch(updateAccount(accounts[0]));
          });
          ethereum.on("chainChanged", () => {
            window.location.reload();
          });
          // Add listeners end
        } else {
          dispatch(connectFailed("Change network to Ethereum"));
        }
      } catch (err) {
        console.log(err)
        dispatch(connectFailed("Something went wrong"));
      }
    } else {

      console.error('Please install MetaMask!')
    }


  };
};

export const updateAccount = (account) => {
  return async (dispatch) => {
    dispatch(updateAccountRequest({
      account: account
    }));
    dispatch(fetchData(account));
  };
};